import React from 'react'
import Typed from 'react-typed';

const About = () => {
  return (
    <div className='bg-[#f0f0f0] w-full py-[50px] mt-10 rounded-[100px] font-[cursive] sm:bg-[#d3e9e9a1]' id='about'>
      {/* name */}
      <p className='font-bold text-[55px] text-center sm:text-[40px]'>
        <span className='border-y-[10px] border-[grey] rounded-[30px] sm:border-[blue]'>
          About Us
        </span>
      </p>

      <p className='text-[60px] text-center mt-[70px] text-[rgba(26,139,185,255)] sm:text-[black] sm:text-[40px]' >
        We Provide<br/>
        <span className='font-bold text-[red] text-[70px] sm:text-[35px]'>
          <Typed strings={["'Best Quality.'", "'Best Quality for Infrastructure.'", "'Best Quality for Pavers.'"]} typeSpeed={300}
            backSpeed={100} loop={true}  className='border-b-[2px] border-[green] rounded-[150px] sm:border-[violet]'  />
        </span>
      </p>
    </div>
  )
}


export  default About;